    animation: rt-fade-in 200ms cubic-bezier(0.16, 1, 0.3, 1);
  }
  .rt-BaseDialogOverlay:where([data-state='closed'])::before {
    opacity: 0;
    animation: rt-fade-out 160ms cubic-bezier(0.16, 1, 0.3, 1);
  }
